@import 'tailwindcss/base';
@import 'tailwindcss/components';
@import 'tailwindcss/utilities';

@font-face {
	font-family: "SfReg";
	src: url(./assets/font/SF-Pro-Display-Regular.otf);
}
@font-face {
	font-family: "SfBold";
	src: url(./assets/font/SF-Pro-Display-Semibold.otf);
}

#root {
	background-color: #fff;
	min-height: 100vh;
}

.w-100 {
	width: -webkit-fill-available;
}

.h-120 {
	height: 35rem
}

.andrea {
	position: absolute;
    height: 100%;
    left: calc(50% - 3px);
    width: 6px;
    background-color: #68707a;
	display: flex;
	justify-content: center;
	align-items: center;
}

.andrea:after {
    content: " ";
    width: 150px;
    height: 150px;
    border: solid 6px #68707a;
    position: absolute;
    border-radius: 50%;
    top: calc(50% - 81px);
    left: calc(50% - 75px);
}

.andrea1{
	position: absolute;
    top: 36%;
    left: 0px;
    transform: translateX(-22%) rotate(-90deg);
}

.andrea3 {
	position: absolute;
    top: 36%;
    right: 0px;
    transform: rotate(90deg) translateY(-39%);
}